<template>
    <div>
        <van-nav-bar :fixed="true" class="common-nav-bar">
            <template #left>
                <img src="../../assets/img/user_icon.png" @click="$router.push({path: '/mine'})" style="height: 18px; width: 18px" alt="">
            </template>
            <template #title>
                <ul>
                    <li :class="activeClass[0]" @click="changeItem(0)">水质</li>
                    <li :class="activeClass[1]" @click="changeItem(1)">空气</li>
                    <li :class="activeClass[2]" @click="changeItem(2)">声环境</li>
                    <li :class="activeClass[3]" @click="changeItem(3)">污染源</li>
                </ul>
            </template>
            <template #right>
                <img src="../../assets/img/search.png" @click="$router.push({path: '/mine'})" style="height: 18px; width: 18px" alt="">
            </template>
        </van-nav-bar>
        <baidu-map class="map" ref="map" :center="options.center" :zoom="options.zoom" center="兴义市" @ready="initMap"></baidu-map>
    </div>
</template>

<script>
    export default {
        name: "mapCom",
        data () {
          return {
            activeClass: ["active"],
            options: {
              zoom: 11,
              center: {
                lng: 104.895830154,
                lat: 25.0965590049
              }
            },
            map: {}
          }
        },
        mounted() {
        },
        methods: {
            changeItem(num){
              this.activeClass      = []
              this.activeClass[num] = "active"
            },
            // 获取百度地图对象
            initMap({BMap, map}){
              this.map = BMap
            }
        }
    }
</script>

<style lang="less" scoped>
    @borderColor: rgba(255, 255, 255, 0.7);
    .map{
        width: 100%;
        height: 100vh;
    }
    .common-nav-bar{
        /deep/ .van-nav-bar__title{
            ul{
                display: flex;
                align-items: center;
                justify-content: center;
                border: 1px solid @borderColor;
                border-radius: 15px;
                padding: 0;
                margin: 0;
                overflow: hidden;
                li{
                    font-size: 14px;
                    padding: 3px 8px;
                    &:not(:last-child){
                        border-right: 1px solid @borderColor;
                    }
                    &.active{
                        background: #fff;
                        color: #216CD5;
                    }
                }
            }
        }
    }
</style>